* {
  margin: 0;
  box-sizing: border-box;
}

img {
  user-select: none;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
}

.center {
  padding: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* flex-direction: column; */
  gap: 20px;
}

.thin {
  font-weight: 300;
}

.square {
  width: min(40vw, 600px);
  /* border: 1px solid #black; */
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  /* align-items: center; */
  background-color: black;
}

.post {
  aspect-ratio: 1/1;
  height: fit-content;
}

.header {
  width: fit-content;
}

.pad {
  padding-inline: 20px;
  width: auto;
  text-align: left;
}

.fields {
  aspect-ratio: inherit;
}

.banner {
  width: 100%;
  height: 40%;
  image-rendering: crisp-edges;
  position: relative;
}

.banner img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.overlay {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to bottom, transparent 0%, transparent 30%, black 100%);
}

.name {
  position: absolute;
  padding: 20px;
  text-align: left;
  bottom: 0;
  font-size: 2.5rem;
  font-weight: 700;
  margin-top: 1rem;
}

.title {
  position: absolute;
  padding: 20px;
  text-align: left;
  bottom: 50px;
  font-size: 1.3rem;
  font-weight: 300;
  margin-top: 1rem;
}

input[type=text] {
  width: 100%;
  color: white;
  font-family: inherit;
  padding: 10px 14px;
  margin: 8px 0;
  box-sizing: border-box;
  border: none;
  border-bottom: 1px solid white;
  background-color: black;
  font-size: inherit;
}

input[type=file] {
  width: 100%;
  color: white;
  font-family: inherit;
  padding: 10px 14px;
  margin: 8px 0;
  box-sizing: border-box;
  border: none;
  border-bottom: 1px solid white;
  background-color: black;
  font-size: inherit;
}

.square span {
  display: flex;
  align-items: center;
  gap: 20px;
}

.hotbar {
  display: flex;
  gap: 20px;
  height: fit-content;
}

.play {
  width: 40px;
  height: fit-content;
  aspect-ratio: 1;
  border-radius: 25px;
  font-size: 0.8rem;
  background-color: #1db954;
  text-align: center;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.follow {
  background: none;
  width: 240px;
  height: fit-content;
  padding: 10px;
  border-radius: 30px;
  text-align: center;
  color: white;
  border: 1px solid white;
  transition: .3s;
}

.follow:hover {
  background-color: white;
  color: black;
  cursor: pointer;
}

.info {
  display: flex;
  gap: 20px;
  padding: 20px;
  text-align: left;
  justify-content: space-between;
}

.info>div {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.picture {
  width: 50%;
  aspect-ratio: 1/1;
  object-fit: cover;
}

@media screen and (max-width: 1200px) {
  .App {
    align-items: center;
    justify-content: flex-start;
  }

  .center {
    flex-direction: column;
    width: min(100vw, 1200px);
    gap: 40px;
  }

  .square {
    width: min(90vw, 600px);
  }

  .play {
    width: 32px;
    font-size: 0.7rem;
  }

  .follow {
    width: 200px;
    height: fit-content;
    font-size: 0.88rem;
  }

  .name {
    font-size: 2rem;
  }

  .title {
    font-size: 1.2rem;
    bottom: 40px;
  }
}

@media screen and (max-width: 600px) {
  .picture {
    width: 40%;
  }

  .procon h2 {
    font-size: 1rem;
  }

  .procon p {
    font-size: 0.8rem;
  }
}

.button {
  padding: 10px 20px;
  border-radius: 30px;
  text-align: center;
  color: black;
  border: 1px solid white;
  background-color: white;
  transition: .3s;
}

.button:hover {
  background-color: black;
  color: white;
  cursor: pointer;
}